.technical-documentation {
  display: flex;
  flex-wrap: wrap;

  // &__title {}

  // &__items {}

  // &__&item {}
  &__element {
    width: 100%;
    display: flex;
  }

  &__form {
    width: 100%;
    display: flex;
    margin-top: 30px;
  }

  .selectbox {
    max-width: 372px;
    height: 46px;
    border-radius: 2px;
    border: 1px solid #cbcbcb;
    background-color: #ffffff;
    position: relative;
    padding: 0 0 0 10px;
    color: #666666;
    
    font-size: 14px;
    line-height: 24px;
    display: flex;
    flex-grow: 1;

    &:first-child {
      margin-right: 24px;
    }
  }

  .selectbox span i {
    width: 20px;
    height: 100%;
    position: absolute;
    right: 0;
    background: url("https://icongr.am/fontawesome/caret-down.svg?color=666666") 0rem/18px no-repeat;
  }

  .selectbox span {
    color: #272727;
    font-size: 13px;
    display: flex;
    height: 100%;
    display: flex;
    align-items: center;
    color: #666666;
    
    font-size: 14px;
    line-height: 24px;
  }

  .selectbox select {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    display: block;
    color: #272727;
    font-size: 13px;
    z-index: 1;
  }

  .selectbox select option {
    padding: 3px 0 3px 10px;
    color: #272727;
    font-size: 13px;
  }

  .shipping-payment__items {
    align-items: baseline;
  }

  .doc_loading {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.71;
    letter-spacing: normal;
    text-align: left;
    color: #666666;
    padding-left: 50px;

    .icon_doc {
      background: url("../images/documentation__pic.svg") no-repeat 100% 100%;
      width: 32px;
      height: 32px;
      margin-right: 10px;
      margin-left: -45px;
    }

    span {
      color: #2e2e2e;
      width: 100%;
    }

    &_first {
      padding: 24px 0 16px 50px;
    }
  }

  .documents__container {
    width: 100%;
  }

  .filter {
    margin-bottom: 125px;
  }

  @media screen and (max-width: 480px) {
    &__form {
      flex-wrap: wrap;
    }

    .selectbox {
      width: 100%;
      margin-right: 0 !important;
      margin-top: 16px;
      max-width: unset;
    }
  }
  @media screen and (max-width: 768px) {
    .shipping-payment__title.articles-main__title {
      margin-top: 15px;
    }
  }
}