.warranty {



  // &__items {}

  // &__item {}

  &__header {
    text-align: left;
  }

  &__text {
    display: flex;
    flex-direction: column
  }

  &__button {
    @include button;
    color: #ffffff;
    font-family: "Roboto Condensed";
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
    margin-bottom: 2rem !important;
  }

  // &__list {}

  // &__li {}
  @media screen and (max-width: 480px) {
    iframe {
      height: 270px;
    }
  }
}